@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;500&display=swap");

/*==============================
	Common styles
==============================*/

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
html,
body {
  height: 100%;
}
body {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  background-color: #16142a;
  -webkit-font-smoothing: antialiased;
}
button {
  padding: 0;
  border: none;
  background-color: transparent;
  transition: 0.5s ease;
  transition-property: color, background-color, border-color, box-shadow;
  cursor: pointer;
}
button:focus {
  outline: none;
}
a {
  text-decoration: none;
  transition: 0.5s ease;
  transition-property: color, background-color, border-color, box-shadow;
}
a:hover,
a:active,
a:focus {
  outline: none;
  text-decoration: none;
  color: white;
}
input,
textarea,
select {
  padding: 0;
  margin: 0;
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-shadow: none;
  transition: 0.5s ease;
  transition-property: background-color, border-color;
}
input:focus,
textarea:focus,
select:focus {
  outline: none;
}
select::-ms-expand {
  display: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
::-moz-selection {
  background: #a034fa;
  color: #16142a;
  text-shadow: none;
}
::selection {
  background: #a034fa;
  color: #16142a;
  text-shadow: none;
}
::-webkit-input-placeholder {
  color: #d0d0d0;
  opacity: 1;
}
::-moz-placeholder {
  color: #d0d0d0;
  opacity: 1;
}
:-moz-placeholder {
  color: #d0d0d0;
  opacity: 1;
}
:-ms-input-placeholder {
  color: #d0d0d0;
  opacity: 1;
}
:focus {
  outline: -webkit-focus-ring-color auto 0px;
}
:focus-visible {
  outline: 0px dotted;
}
@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1140px;
  }
}
.body--home {
  background: url("./component/asset/img/bg.png") no-repeat top center/cover
    #16142a;
}

body::-webkit-scrollbar {
  width: 16px;
}
body::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
}
body::-webkit-scrollbar-thumb {
  background-color: #16142a;
  outline: 0 solid rgba(255, 255, 255, 0.05);
}
.row--relative {
  position: relative;
}
/*==============================
	Header
==============================*/
.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  border-bottom: 2px solid rgba(255, 255, 255, 0.05);
  background: linear-gradient(
    150deg,
    rgba(255, 255, 255, 0.03) 0%,
    rgba(255, 255, 255, 0.01) 100%
  );
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  transition: background 0.5s ease;
}
.header.active {
  background: #16142a;
}
.header__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  height: 80px;
  width: 100%;
}
.header__logo {
  z-index: 1;
  width: auto;
  height: 50px;
  display: inline-flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-left: 40px;
}
.header__logo img {
  height: 50px;
  width: auto;
}
.header__btn {
  position: absolute;
  width: 24px;
  height: 22px;
  display: block;
  left: 0;
}
.header__btn span {
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #fff;
  border-radius: 2px;
  transition: 0.5s ease;
  opacity: 1;
}
.header__btn span:first-child {
  top: 0;
}
.header__btn span:nth-child(2) {
  top: 10px;
  width: 16px;
}
.header__btn span:last-child {
  top: 20px;
  width: 8px;
}
.header__btn--active span:first-child {
  transform: rotate(45deg);
  top: 10px;
}
.header__btn--active span:nth-child(2) {
  opacity: 0;
}
.header__btn--active span:last-child {
  width: 24px;
  transform: rotate(-45deg);
  top: 10px;
}
.header__tagline {
  display: none;
}
.header__language {
  position: relative;
  display: block;
  width: auto;
  margin-left: auto;
  margin-right: 30px;
}
.header__language a.dropdown-link {
  display: inline-flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 14px;
  line-height: 24px;
  height: 40px;
  font-weight: 400;
  color: #fff;
  text-transform: uppercase;
}
.header__language a.dropdown-link i {
  font-size: 8px;
  margin-left: 5px;
  margin-top: 2px;
}
.header__language a.dropdown-link:hover,
.header__language a.dropdown-link[aria-expanded="true"] {
  color: #a034fa;
}
.header__language-menu {
  position: absolute;
  background-color: #16142a;
  padding: 20px;
  display: flex !important;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-width: 110px;
  text-align: left;
  z-index: -1;
  pointer-events: none;
  opacity: 0;
  border-radius: 10px;
  transition: 0.5s ease;
  transition-property: opacity, margin-top, transform;
  top: 0;
  transform: translate3d(0px, 16px, 0px);
  border: 2px solid rgba(255, 255, 255, 0.05);
}
.header__language-menu li {
  position: relative;
  padding: 0;
  margin-bottom: 12px;
}
.header__language-menu li:last-child {
  margin-bottom: 0;
}
.header__language-menu a {
  font-size: 14px;
  line-height: 24px;
  color: #fff;
  text-transform: none;
  font-weight: 400;
}
.header__language-menu a:hover {
  color: #a034fa;
}
.header__language-menu.show {
  z-index: 1000;
  pointer-events: auto;
  opacity: 1;
}
.header__nav {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 280px;
  height: calc(100vh - 82px);
  top: 82px;
  left: 0;
  background: #16142a;
  padding: 30px 15px 0;
  transition: transform 0.5s ease;
  transform: translate3d(-280px, 0, 0);
  border-right: 2px solid rgba(255, 255, 255, 0.05);
}
.header__nav--active {
  transform: translate3d(0, 0, 0);
}
.header__nav li {
  display: block;
  margin-bottom: 25px;
}
.header__nav li:last-child {
  margin-bottom: 0;
}
.header__nav a {
  display: inline-flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: #fff;
  text-transform: uppercase;
}
.header__nav a i {
  font-size: 8px;
  margin-left: 5px;
  margin-top: 2px;
}
.header__nav a.dropdown-link--menu {
  height: 24px;
}
.header__nav a.dropdown-link--menu i {
  font-size: 20px;
  margin-left: 0;
}
.header__nav a:hover,
.header__nav a[aria-expanded="true"] {
  color: #a034fa;
}
.header__dropdown {
  position: relative;
  display: block;
  width: auto;
}
.header__dropdown-menu {
  position: absolute;
  background-color: #16142a;
  padding: 20px;
  display: flex !important;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-width: 140px;
  text-align: left;
  z-index: -1;
  pointer-events: none;
  opacity: 0;
  border-radius: 10px;
  transition: 0.5s ease;
  transition-property: opacity, margin-top, transform;
  top: 0;
  transform: translate3d(0px, 16px, 0px);
  border: 2px solid rgba(255, 255, 255, 0.05);
}
.header__dropdown-menu li {
  position: relative;
  padding: 0;
  margin-bottom: 12px;
}
.header__dropdown-menu li:last-child {
  margin-bottom: 0;
}
.header__dropdown-menu a {
  font-size: 14px;
  line-height: 24px;
  color: #fff;
  text-transform: none;
  font-weight: 400;
  white-space: nowrap;
}
.header__dropdown-menu a:hover {
  color: #a034fa;
}
.header__dropdown-menu.show {
  z-index: 1000;
  pointer-events: auto;
  opacity: 1;
}
.header__profile {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 46px;
  height: 46px;
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  background: linear-gradient(135deg, #572c7c 0%, #572c7c 50%, #a034fa 100%);
}
.header__profile span {
  display: none;
}
.header__profile i {
  font-size: 24px;
  color: #fff;
}
.header__profile:hover {
  border-color: #a034fa;
}
@media (min-width: 576px) {
  .header__logo {
    margin-left: 45px;
  }
  .header__tagline {
    display: block;
    line-height: 24px;
    color: #d0d0d0;
    font-size: 14px;
    margin-right: auto;
    margin-left: 20px;
    font-weight: 300;
  }
  .header__nav {
    padding-left: calc((100vw - 516px) / 2);
    width: 320px;
    transform: translate3d(-320px, 0, 0);
  }
  .header__nav--active {
    transform: translate3d(0, 0, 0);
  }
}
@media (min-width: 768px) {
  .header__language {
    margin-right: 44px;
  }
  .header__profile {
    min-width: 130px;
    padding: 0 20px;
    width: auto;
  }
  .header__profile span {
    display: block;
    text-transform: uppercase;
    font-size: 14px;
    color: #fff;
    transition: 0.5s ease;
    font-weight: 500;
  }
  .header__profile i {
    display: none;
  }
  .header__profile:before,
  .header__profile:after {
    content: "";
    position: absolute;
    pointer-events: none;
    width: 26px;
    height: 30px;
  }
  .header__profile:before {
    background: url("./component/asset/img/btn/left--small.svg") no-repeat
      center;
    left: 8px;
    transition: left 0.5s ease;
  }
  .header__profile:after {
    background: url("./component/asset/img/btn/right--small.svg") no-repeat
      center;
    right: 8px;
    transition: right 0.5s ease;
  }
  .header__profile:hover:before {
    left: 12px;
  }
  .header__profile:hover:after {
    right: 12px;
  }
  .header__nav {
    padding-left: calc((100vw - 696px) / 2);
  }
}
@media (min-width: 992px) {
  .header__nav {
    padding-left: calc((100vw - 936px) / 2);
  }
}
@media (min-width: 1200px) {
  .header__btn {
    display: none;
  }
  .header__logo {
    margin-left: 0;
  }
  .header__language {
    margin-left: 0;
  }
  .header__nav {
    position: relative;
    flex-direction: row;
    align-items: center;
    width: auto;
    height: 80px;
    top: auto;
    left: auto;
    background: transparent;
    padding: 0;
    transition: 0s;
    transform: translate3d(0, 0, 0);
    border-right: none;
    z-index: 2;
    margin-right: auto;
  }
  .header__nav li {
    margin-right: 44px;
    margin-bottom: 0;
  }
  .header__nav li:last-child {
    margin-right: 0;
  }
  .header__nav a {
    justify-content: center;
    height: 40px;
    text-transform: none;
  }
  .header__nav a.dropdown-link--menu {
    height: 40px;
  }
  .header__dropdown-menu li {
    margin-right: 0;
    margin-bottom: 12px;
    padding: 0;
  }
  .header__dropdown-menu li:last-child {
    margin-bottom: 0;
  }
  .header__dropdown-menu a {
    height: auto;
    color: #fff;
    justify-content: flex-start;
  }
  .header__dropdown-menu a:hover {
    color: #a034fa;
  }
  .header__profile {
    z-index: 1;
  }
}
@media (min-width: 1400px) {
  .header:before,
  .header:after {
    content: "";
    position: absolute;
    display: block;
    width: 67px;
    height: 20px;
    top: 30px;
    pointer-events: none;
  }
  .header:before {
    left: 30px;
    /* background: url("../img/dodgers/screw--header-left.svg") no-repeat center; */
    background: url("./component/asset/img/dodgers/screw--header-left.svg")
      no-repeat center;
  }

  .header:after {
    right: 30px;
    background: url("./component/asset/img/dodgers/screw--header-right.svg")
      no-repeat center;
  }
}

/*==============================
	Hero
==============================*/
.hero {
  margin-top: 80px;
  padding: 100px 0 36px;
}
.hero__title {
  font-size: 32px;
  line-height: 48px;
  text-transform: uppercase;
  color: #fff;
  font-weight: 300;
  margin-bottom: 0;
}
.hero__title strong,
.hero__title b {
  font-weight: 500;
}
.hero__text {
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  color: #d0d0d0;
  margin-bottom: 16px;
  margin-top: 15px;
}
.hero__text a {
  color: #fff;
  border-bottom: 2px solid #a034fa;
}
.hero__text a:hover {
  border-color: transparent;
}
.hero__text strong,
.hero__text b {
  font-weight: 500;
}
.hero__btns {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 40px;
}
.hero__btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: calc(50% - 15px);
  height: 60px;
  border-radius: 10px;
  border: 2px solid rgba(255,255,255,0.3);
  background: linear-gradient(135deg, #572c7c 0%, #572c7c 50%, #a034fa 100%);
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  position: relative;
}
.hero__btn--light {
  background: transparent;
}
.hero__btn:before,
.hero__btn:after {
  content: '';
  position: absolute;
  pointer-events: none;
  width: 28px;
  height: 40px;
}
.hero__btn:before {
  /* background: url("../img/btn/left.svg") no-repeat center; */
  background: url("./component/asset/img/btn/left.svg") no-repeat center;

  left: 10px;

  transition: left 0.5s ease;
}
.hero__btn:after {
  background: url("./component/asset/img/btn/right.svg") no-repeat center;
  right: 10px;
  transition: right 0.5s ease;
}
.hero__btn:hover {
  border-color: #a034fa;
}
.hero__btn:hover:before {
  left: 15px;
}
.hero__btn:hover:after {
  right: 15px;
}
.hero__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}
.hero__content--second {
  margin-top: 60px;
}
@media (min-width: 576px) {
  .hero__title {
    font-size: 36px;
    line-height: 52px;
  }
  .hero__btns {
    justify-content: flex-start;
  }
  .hero__btn {
    width: 200px;
    margin-right: 30px;
  }
  .hero__btn:last-child {
    margin-right: 0;
  }
}
@media (min-width: 768px) {
  .hero {
    padding: 120px 0 46px;
  }
  .hero__title {
    font-size: 42px;
    line-height: 58px;
  }
  .hero__text {
    font-size: 18px;
    line-height: 30px;
  }
  .hero__btns {
    margin-top: 50px;
  }
  .hero__btn {
    margin-right: 40px;
  }
  .hero__btn:last-child {
    margin-right: 0;
  }
  .hero__content--second {
    margin-top: 70px;
  }
}
@media (min-width: 992px) {
  .hero__content--second {
    margin-top: 0;
  }
}
@media (min-width: 1200px) {
  .hero {
    padding: 150px 0 76px;
  }
  .hero__title {
    font-size: 44px;
    line-height: 60px;
  }
  .hero__content--second {
    margin-top: 0;
  }
}

/*==============================
	Stats
==============================*/
.stats {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 130px;
  margin-top: 24px;
  border-radius: 10px;
  border: 2px solid rgba(255,255,255,0.05);
  background: linear-gradient(150deg, rgba(255,255,255,0.03) 0%, rgba(255,255,255,0.01) 100%);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  position: relative;
}
.stats:before,
.stats:after {
  content: '';
  position: absolute;
  top: 22px;
  width: 36px;
  height: 2px;
  display: block;
  pointer-events: none;
}
.stats:before {
  left: 46px;
  background: linear-gradient(90deg, rgba(255,255,255,0.05) 0%, rgba(255,255,255,0) 100%);
}
.stats:after {
  right: 46px;
  background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.05) 100%);
}
.stats__value {
  font-size: 32px;
  line-height: 100%;
  font-weight: 400;
  color: #fff;
  margin-top: 15px;
  margin-bottom: 10px;
}
.stats__name {
  font-size: 14px;
  line-height: 24px;
  color: #d0d0d0;
  margin-bottom: 0;
}
.stats__dodger {
  position: absolute;
  display: block;
  width: 16px;
  height: 63px;
  top: 15px;
  pointer-events: none;
}
.stats__dodger--left {
  left: 15px;
}
.stats__dodger--right {
  right: 15px;
}
.stats__dodger--purple {
  background: url("./component/asset/img/dodgers/stats--purple.svg") no-repeat center;
}
.stats__dodger--orange {
  background: url("./component/asset/img/dodgers/stats--orange.svg") no-repeat center;
}
.stats__dodger--green {
  background: url("./component/asset/img/dodgers/stats--green.svg") no-repeat center;
}
.stats__dodger--blue {
  background: url("./component/asset/img/dodgers/stats--blue.svg") no-repeat center;
}
.stats__dodger--red {
  background: url("./component/asset/img/dodgers/stats--red.svg") no-repeat center;
}

/*==============================
	Features
==============================*/
.feature {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 215px;
  margin-top: 24px;
  border-radius: 25px 10px 10px 10px;
  border: 2px solid rgba(255,255,255,0.05);
  background: linear-gradient(150deg, rgba(255,255,255,0.03) 0%, rgba(255,255,255,0.01) 100%);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  position: relative;
  padding: 0 55px;
}
.feature__title {
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  color: #fff;
  margin-bottom: 12px;
  text-align: center;
}
.feature__text {
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  color: #d0d0d0;
  margin-bottom: 0;
  text-align: center;
}
.feature__text b,
.feature__text strong {
  color: #fff;
  font-weight: 500;
}

/*==============================
	Block icon
==============================*/
.block-icon {
  position: absolute;
  top: -2px;
  left: -2px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 50px;
  border: 2px solid;
  border-radius: 50%;
}
.block-icon i {
  font-size: 22px;
  color: #fff;
}
.block-icon:before {
  content: '';
  position: absolute;
  pointer-events: none;
  width: 74px;
  height: 74px;
  top: 50%;
  left: 50%;
}
.block-icon--purple {
  border-color: #a034fa;
}
.block-icon--purple:before {
  background: url("./component/asset/img/dodgers/dots--purple.svg");
}
.block-icon--orange {
  border-color: #e66c12;
}
.block-icon--orange:before {
  background: url("./component/asset/img/dodgers/dots--orange.svg") no-repeat center;
}
.block-icon--green {
  border-color: #39dc01;
}
.block-icon--green:before {
  background: url("./component/asset/img/dodgers/dots--green.svg") no-repeat center;
}
.block-icon--blue {
  border-color: #4399fc;
}
.block-icon--blue:before {
  background: url("./component/asset/img/dodgers/dots--blue.svg") no-repeat center;
}
.block-icon--red {
  border-color: #e2293b;
}
.block-icon--red:before {
  background: url("./component/asset/img/dodgers/dots--red.svg") no-repeat center;
}
.block-icon--yellow {
  border-color: #d8be15;
}
.block-icon--yellow:before {
  background: url("./component/asset/img/dodgers/dots--yellow.svg") no-repeat center;
}
/*==============================
	Screw
==============================*/
.screw {
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  background: url("./component/asset/img/dodgers/screw.svg") no-repeat center;
