}
.section--head {
  margin-top: 80px;
  padding: 60px 0 20px;
}
.section--pb {
  padding-bottom: 26px;
}
.section--content {
  padding: 0;
}
.section__title {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.section__title h1,
.section__title h2 {
  font-size: 30px;
  line-height: 40px;
  font-weight: 300;
  color: #fff;
  margin-bottom: 16px;
  text-align: center;
  position: relative;
  width: auto;
  padding: 0 50px;
}
.section__title h1:before,
.section__title h2:before,
.section__title h1:after,
.section__title h2:after {
  content: '';
  position: absolute;
  display: block;
  width: 30px;
  height: 30px;
  pointer-events: none;
  top: 50%;
  margin-top: -12px;
}
.section__title h1:before,
.section__title h2:before {
  left: 0;
  background: url("./component/asset/img/dodgers/title--left.svg");
}
.section__title h1:after,
.section__title h2:after {
  right: 0;
  background: url("./component/asset/img/dodgers/title--right.svg");
}
.section__title p {
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  color: #d0d0d0;
  margin-bottom: 16px;
  text-align: center;
}
.section__title p a {
  color: #fff;
  border-bottom: 2px solid #a034fa;
}
.section__title p a:hover {
  border-color: transparent;
}
.section__canvas {
  position: absolute;
  top: 0;
  right: 40px;
  left: 40px;
  bottom: -24px;
  width: auto;
  max-width: none;
  padding: 0;
  z-index: 1;
  border-radius: 10px;
  overflow: hidden;
  opacity: 0.25;
}
.section__canvas--full {
  right: 0;
  left: 0;
  bottom: 0;
  border-radius: 0;
}
.section__tabs {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  overflow-x: auto;
  width: 100%;
  border: none;
  margin-bottom: 16px;
  margin-top: 24px;
}
.section__tabs li {
  margin-right: 25px;
}
.section__tabs li:last-child {
  margin-right: 0;
}
.section__tabs button {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  color: #d0d0d0;
  position: relative;
  font-size: 14px;
  height: 40px;
  font-weight: 400;
  white-space: nowrap;
}
.section__tabs button:hover {
  color: #fff;
}
.section__tabs button:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0;
  display: block;
  background: #a034fa;
  box-shadow: none;
  transition: 0.5s ease;
  border-radius: 3px 3px 0 0;
}
.section__tabs button.active {
  color: #fff;
}
.section__tabs button.active:before {
  height: 2px;
}
.section__tabs--big {
  justify-content: flex-start;
}
.section__tabs--left {
  justify-content: flex-start;
  margin-top: 0;
}
.section__btns {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 40px;
}
