  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 180px;
  height: 60px;
  border-radius: 10px;
  border: 2px solid rgba(255,255,255,0.3);
  background: linear-gradient(135deg, #572c7c 0%, #572c7c 50%, #a034fa 100%);
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  position: relative;
}
