.section__btn:after {
  content: '';
  position: absolute;
  pointer-events: none;
  width: 28px;
  height: 40px;
}
.section__btn:before {
  background: url("./component/asset/img/btn/left.svg") no-repeat center;
  left: 10px;
  transition: left 0.5s ease;
}
.section__btn:after {
  background: url("./component/asset/img/btn/right.svg") no-repeat center;
  right: 10px;
  transition: right 0.5s ease;
}
.section__btn:hover {
  border-color: #a034fa;
}
.section__btn:hover:before {
  left: 15px;
}
.section__btn:hover:after {
  right: 15px;
}
.section__content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
