  background: linear-gradient(150deg, rgba(255,255,255,0.03) 0%, rgba(255,255,255,0.01) 100%);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  position: relative;
  padding: 60px 60px 50px;
  z-index: 2;
}
.page-error__title {
  position: relative;
  color: #a034fa;
  line-height: 100%;
  font-size: 120px;
  margin-bottom: 10px;
  font-weight: 500;
  text-align: center;
  width: 100%;
}
.page-error__text {
  text-align: center;
  display: block;
  width: 100%;
  color: #d0d0d0;
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 30px;
}
.page-error__btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  border-radius: 10px;
  border: 2px solid rgba(255,255,255,0.3);
  background: linear-gradient(135deg, #572c7c 0%, #572c7c 50%, #a034fa 100%);
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  position: relative;
}
.page-error__btn:before,
.page-error__btn:after {
  content: '';
  position: absolute;
  pointer-events: none;
  width: 28px;
  height: 40px;
}
.page-error__btn:before {
  background: url("./component/asset/img/btn/left.svg") no-repeat center;
  left: 10px;
  transition: left 0.5s ease;
}
.page-error__btn:after {
  background: url("./component/asset/img/btn/right.svg") no-repeat center;
  right: 10px;
  transition: right 0.5s ease;
}
.page-error__btn:hover {
  border-color: #a034fa;
}
.page-error__btn:hover:before {
  left: 15px;
}
.page-error__btn:hover:after {
  right: 15px;
}
