/*==============================
	Accordion
==============================*/
.accordion__card {
  margin-top: 24px;
}
.accordion__card button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  border: 2px solid rgba(255,255,255,0.05);
  background: linear-gradient(150deg, rgba(255,255,255,0.03) 0%, rgba(255,255,255,0.01) 100%);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 25px;
  min-height: 50px;
  padding: 10px 65px;
  color: #fff;
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  text-align: center;
}
.accordion__card button:before,
.accordion__card button:after {
  content: '';
  position: absolute;
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-color: transparent;
  border: 2px solid rgba(255,255,255,0.05);
  /* background: url("../img/dodgers/screw.svg") no-repeat center; */
  background: url('./component/asset/img/dodgers/screw.svg') no-repeat center;
  transition: border-color 0.5s ease;
}
.accordion__card button:before {
  top: -2px;
  left: -2px;
}
.accordion__card button:after {
  top: -2px;
  right: -2px;
}
.accordion__card button:hover:before,
.accordion__card button:hover:after {
  border-color: #a034fa;
}
.accordion__card button[aria-expanded="true"]:before,
.accordion__card button[aria-expanded="true"]:after {
  border-color: #a034fa;
}
.accordion__card p {
  padding: 20px 24px 0;
  color: #d0d0d0;
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 0;
  text-align: center;
}
